'use client';

import { useState } from 'react';
import { TabContainer, Tab } from '@/components/shared';
import EstructuraEquipos from './EstructuraEquipos';
import GestionStaff from './GestionStaff';
import AccesosCliente from './AccesosCliente';
import styles from './Usuarios.module.css';

export default function Usuarios() {
  const [activeTab, setActiveTab] = useState<string>('equipos');
  const [isRefreshing, setIsRefreshing] = useState(false);

  const handleRefresh = async () => {
    setIsRefreshing(true);
    // TODO: Implement API refresh
    await new Promise(resolve => setTimeout(resolve, 1000));
    setIsRefreshing(false);
  };

  const tabs: Tab[] = [
    {
      id: 'equipos',
      label: 'Estructura de Equipos',
      content: <EstructuraEquipos />,
    },
    {
      id: 'staff',
      label: 'Gestión de Staff',
      content: <GestionStaff />,
    },
    {
      id: 'accesos',
      label: 'Accesos de Cliente',
      content: <AccesosCliente />,
    },
  ];

  return (
    <div className={styles.usuarios}>
      <div className={styles.usuariosContent}>
        <div className={styles.tabContent}>
          <TabContainer
            tabs={tabs}
            activeTab={activeTab}
            onTabChange={setActiveTab}
            showRefresh={true}
            onRefresh={handleRefresh}
            isRefreshing={isRefreshing}
          />
        </div>
      </div>
    </div>
  );
}

