'use client';

import { useState } from 'react';
import styles from './GestionStaff.module.css';

interface StaffMember {
  id: string;
  full_name: string;
  role: string;
  base_commission: number;
  email: string;
  telegram_chat_id: string;
  is_active: boolean;
}

export default function GestionStaff() {
  const [staff, setStaff] = useState<StaffMember[]>([
    {
      id: '1',
      full_name: 'María González',
      role: 'accountant',
      base_commission: 0.15,
      email: 'maria.gonzalez@cinpro.com',
      telegram_chat_id: '@maria_gonzalez',
      is_active: true,
    },
    {
      id: '2',
      full_name: 'Juan Pérez',
      role: 'senior_accountant',
      base_commission: 0.20,
      email: 'juan.perez@cinpro.com',
      telegram_chat_id: '',
      is_active: true,
    },
    {
      id: '3',
      full_name: 'Laura Sánchez',
      role: 'accountant',
      base_commission: 0.12,
      email: 'laura.sanchez@cinpro.com',
      telegram_chat_id: '@laura_sanchez',
      is_active: false,
    },
  ]);

  // TODO: Fetch from API
  const staffRoles = [
    { value: 'accountant', label: 'Contador' },
    { value: 'senior_accountant', label: 'Contador Senior' },
    { value: 'manager', label: 'Gerente' },
    { value: 'director', label: 'Director' },
  ];

  const handleFieldChange = (id: string, field: keyof StaffMember, value: string | number | boolean) => {
    setStaff(staff.map(member => 
      member.id === id ? { ...member, [field]: value } : member
    ));
  };

  const handleTestTelegram = async (id: string, chatId: string) => {
    if (!chatId) {
      alert('Por favor ingrese un Telegram Chat ID');
      return;
    }
    // TODO: Implement API call to test Telegram message
    alert(`Enviando mensaje de prueba a ${chatId}...`);
  };

  const formatCommission = (value: number): string => {
    return `${(value * 100).toFixed(0)}%`;
  };

  const parseCommission = (value: string): number => {
    const num = parseFloat(value);
    if (isNaN(num)) return 0;
    return Math.max(0, Math.min(1, num / 100));
  };

  return (
    <div className={styles.gestionStaff}>
      <div className={styles.tableContainer}>
        <table className={styles.staffTable}>
          <thead>
            <tr>
              <th>Nombre Completo</th>
              <th>Rol</th>
              <th>Comisión Base</th>
              <th>Email</th>
              <th>Telegram Chat ID</th>
              <th>Estado</th>
            </tr>
          </thead>
          <tbody>
            {staff.map((member) => (
              <tr key={member.id}>
                <td className={styles.readOnlyCell}>{member.full_name}</td>
                
                <td>
                  <select
                    className={styles.select}
                    value={member.role}
                    onChange={(e) => handleFieldChange(member.id, 'role', e.target.value)}
                  >
                    {staffRoles.map((role) => (
                      <option key={role.value} value={role.value}>
                        {role.label}
                      </option>
                    ))}
                  </select>
                </td>

                <td>
                  <div className={styles.commissionInput}>
                    <input
                      type="number"
                      className={styles.numberInput}
                      min="0"
                      max="100"
                      step="0.01"
                      value={member.base_commission * 100}
                      onChange={(e) => {
                        const value = parseCommission(e.target.value);
                        handleFieldChange(member.id, 'base_commission', value);
                      }}
                    />
                    <span className={styles.percentDisplay}>
                      {formatCommission(member.base_commission)}
                    </span>
                  </div>
                </td>

                <td className={styles.readOnlyCell}>{member.email}</td>

                <td>
                  <div className={styles.telegramInput}>
                    <input
                      type="text"
                      className={styles.textInput}
                      value={member.telegram_chat_id}
                      onChange={(e) => handleFieldChange(member.id, 'telegram_chat_id', e.target.value)}
                      placeholder="@username o ID"
                    />
                    <button
                      className={styles.testBtn}
                      onClick={() => handleTestTelegram(member.id, member.telegram_chat_id)}
                      title="Enviar Mensaje de Prueba"
                    >
                      <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                        <path d="M22 2L11 13" />
                        <path d="M22 2l-7 20-4-9-9-4 20-7z" />
                      </svg>
                    </button>
                  </div>
                </td>

                <td>
                  <label className={styles.toggleSwitch}>
                    <input
                      type="checkbox"
                      checked={member.is_active}
                      onChange={(e) => handleFieldChange(member.id, 'is_active', e.target.checked)}
                    />
                    <span className={styles.toggleSlider}></span>
                  </label>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

