'use client';

import { useState } from 'react';
import styles from './EditGroupModal.module.css';

interface EditGroupModalProps {
  group: {
    id: string;
    group_name: string;
    chief: {
      id: string;
      first_name: string;
      last_name: string;
    };
  };
  onClose: () => void;
  onUpdate: (groupId: string, newChiefId: string) => void;
}

export default function EditGroupModal({ group, onClose, onUpdate }: EditGroupModalProps) {
  const [selectedChiefId, setSelectedChiefId] = useState<string>(group.chief.id);

  // TODO: Fetch actual staff list from API
  const availableStaff = [
    { id: '1', name: 'Carlos Ramírez' },
    { id: '2', name: 'Ana Martínez' },
    { id: '3', name: 'Luis Fernández' },
    { id: '4', name: 'María García' },
  ];

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onUpdate(group.id, selectedChiefId);
  };

  return (
    <div className={styles.overlay} onClick={onClose}>
      <div className={styles.modal} onClick={(e) => e.stopPropagation()}>
        <div className={styles.modalHeader}>
          <h2 className={styles.modalTitle}>Editar Grupo: {group.group_name}</h2>
          <button className={styles.closeBtn} onClick={onClose}>
            <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <line x1="18" y1="6" x2="6" y2="18" />
              <line x1="6" y1="6" x2="18" y2="18" />
            </svg>
          </button>
        </div>

        <form className={styles.modalContent} onSubmit={handleSubmit}>
          <div className={styles.formGroup}>
            <label htmlFor="chief" className={styles.label}>
              Jefe del Grupo
            </label>
            <select
              id="chief"
              className={styles.select}
              value={selectedChiefId}
              onChange={(e) => setSelectedChiefId(e.target.value)}
            >
              {availableStaff.map((staff) => (
                <option key={staff.id} value={staff.id}>
                  {staff.name}
                </option>
              ))}
            </select>
          </div>

          <div className={styles.modalActions}>
            <button type="button" className={styles.cancelBtn} onClick={onClose}>
              Cancelar
            </button>
            <button type="submit" className={styles.submitBtn}>
              Guardar Cambios
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

