'use client';

import { useState } from 'react';
import styles from './LinkEntityModal.module.css';

interface LinkEntityModalProps {
  userId: string;
  onClose: () => void;
  onSubmit: (data: {
    entityId: string;
    permission: 'OWNER' | 'VIEW_ONLY';
    isPrimary: boolean;
  }) => void;
}

export default function LinkEntityModal({ userId, onClose, onSubmit }: LinkEntityModalProps) {
  const [selectedEntityId, setSelectedEntityId] = useState<string>('');
  const [permission, setPermission] = useState<'OWNER' | 'VIEW_ONLY'>('VIEW_ONLY');
  const [isPrimary, setIsPrimary] = useState<boolean>(false);
  const [searchQuery, setSearchQuery] = useState<string>('');

  // TODO: Fetch from API with search
  const availableEntities = [
    { id: '101', name: 'Empresa ABC S.A.' },
    { id: '102', name: 'Empresa XYZ Ltda.' },
    { id: '103', name: 'Compañía DEF Inc.' },
    { id: '104', name: 'Corporación GHI S.A.' },
  ].filter(entity => 
    entity.name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedEntityId) {
      alert('Por favor seleccione una entidad');
      return;
    }
    onSubmit({
      entityId: selectedEntityId,
      permission,
      isPrimary,
    });
  };

  return (
    <div className={styles.overlay} onClick={onClose}>
      <div className={styles.modal} onClick={(e) => e.stopPropagation()}>
        <div className={styles.modalHeader}>
          <h2 className={styles.modalTitle}>Vincular Entidad</h2>
          <button className={styles.closeBtn} onClick={onClose}>
            <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <line x1="18" y1="6" x2="6" y2="18" />
              <line x1="6" y1="6" x2="18" y2="18" />
            </svg>
          </button>
        </div>

        <form className={styles.modalContent} onSubmit={handleSubmit}>
          <div className={styles.formGroup}>
            <label htmlFor="entity" className={styles.label}>
              Seleccionar Entidad
            </label>
            <input
              type="text"
              className={styles.searchInput}
              placeholder="Buscar entidad..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
            <select
              id="entity"
              className={styles.select}
              value={selectedEntityId}
              onChange={(e) => setSelectedEntityId(e.target.value)}
            >
              <option value="">-- Seleccione una entidad --</option>
              {availableEntities.map((entity) => (
                <option key={entity.id} value={entity.id}>
                  {entity.name}
                </option>
              ))}
            </select>
          </div>

          <div className={styles.formGroup}>
            <label className={styles.label}>Permiso</label>
            <div className={styles.radioGroup}>
              <label className={styles.radioLabel}>
                <input
                  type="radio"
                  name="permission"
                  value="OWNER"
                  checked={permission === 'OWNER'}
                  onChange={(e) => setPermission(e.target.value as 'OWNER' | 'VIEW_ONLY')}
                />
                <span>Propietario</span>
              </label>
              <label className={styles.radioLabel}>
                <input
                  type="radio"
                  name="permission"
                  value="VIEW_ONLY"
                  checked={permission === 'VIEW_ONLY'}
                  onChange={(e) => setPermission(e.target.value as 'OWNER' | 'VIEW_ONLY')}
                />
                <span>Solo Lectura</span>
              </label>
            </div>
          </div>

          <div className={styles.formGroup}>
            <label className={styles.checkboxLabel}>
              <input
                type="checkbox"
                checked={isPrimary}
                onChange={(e) => setIsPrimary(e.target.checked)}
              />
              <span>Marcar como Entidad Principal</span>
            </label>
          </div>

          <div className={styles.modalActions}>
            <button type="button" className={styles.cancelBtn} onClick={onClose}>
              Cancelar
            </button>
            <button type="submit" className={styles.submitBtn}>
              Vincular
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

