'use client';

import { useState } from 'react';
import LinkEntityModal from './LinkEntityModal';
import styles from './AccesosCliente.module.css';

interface ClientUser {
  id: string;
  email: string;
  full_name: string;
}

interface LinkedEntity {
  id: string;
  entity_id: string;
  entity_name: string;
  permission: 'OWNER' | 'VIEW_ONLY';
  is_primary: boolean;
}

export default function AccesosCliente() {
  const [selectedUser, setSelectedUser] = useState<string | null>(null);
  const [isLinkModalOpen, setIsLinkModalOpen] = useState(false);

  // TODO: Fetch from API
  const clientUsers: ClientUser[] = [
    { id: '1', email: 'cliente1@example.com', full_name: 'Juan Cliente' },
    { id: '2', email: 'cliente2@example.com', full_name: 'María Cliente' },
    { id: '3', email: 'cliente3@example.com', full_name: 'Pedro Cliente' },
  ];

  // TODO: Fetch from API based on selectedUser
  const linkedEntities: Record<string, LinkedEntity[]> = {
    '1': [
      {
        id: '1',
        entity_id: '101',
        entity_name: 'Empresa ABC S.A.',
        permission: 'OWNER',
        is_primary: true,
      },
      {
        id: '2',
        entity_id: '102',
        entity_name: 'Empresa XYZ Ltda.',
        permission: 'VIEW_ONLY',
        is_primary: false,
      },
    ],
    '2': [
      {
        id: '3',
        entity_id: '103',
        entity_name: 'Compañía DEF Inc.',
        permission: 'OWNER',
        is_primary: true,
      },
    ],
    '3': [],
  };

  const currentLinkedEntities = selectedUser ? linkedEntities[selectedUser] || [] : [];

  const handleLinkEntity = async (data: {
    entityId: string;
    permission: 'OWNER' | 'VIEW_ONLY';
    isPrimary: boolean;
  }) => {
    if (!selectedUser) return;

    // TODO: Implement API call to link entity
    // await fetch(`/api/client_user_access`, {
    //   method: 'POST',
    //   body: JSON.stringify({
    //     user_id: selectedUser,
    //     entity_id: data.entityId,
    //     permission: data.permission,
    //     is_primary: data.isPrimary,
    //   }),
    // });

    setIsLinkModalOpen(false);
    // Refresh data
  };

  const handleRemoveLink = async (linkId: string) => {
    // TODO: Implement API call to remove link
    // await fetch(`/api/client_user_access/${linkId}`, {
    //   method: 'DELETE',
    // });
    // Refresh data
  };

  return (
    <div className={styles.accesosCliente}>
      <div className={styles.masterDetail}>
        {/* Master: User List */}
        <div className={styles.masterPanel}>
          <h3 className={styles.panelTitle}>Usuarios Cliente</h3>
          <div className={styles.userList}>
            {clientUsers.map((user) => (
              <div
                key={user.id}
                className={`${styles.userItem} ${selectedUser === user.id ? styles.active : ''}`}
                onClick={() => setSelectedUser(user.id)}
              >
                <div className={styles.userInfo}>
                  <p className={styles.userName}>{user.full_name}</p>
                  <p className={styles.userEmail}>{user.email}</p>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Detail: Linked Entities */}
        <div className={styles.detailPanel}>
          {selectedUser ? (
            <>
              <div className={styles.detailHeader}>
                <h3 className={styles.panelTitle}>
                  Entidades Vinculadas
                </h3>
                <button
                  className={styles.addBtn}
                  onClick={() => setIsLinkModalOpen(true)}
                >
                  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <line x1="12" y1="5" x2="12" y2="19" />
                    <line x1="5" y1="12" x2="19" y2="12" />
                  </svg>
                  Vincular Entidad
                </button>
              </div>

              <div className={styles.linkedEntities}>
                {currentLinkedEntities.length === 0 ? (
                  <div className={styles.emptyState}>
                    <p>No hay entidades vinculadas</p>
                    <button
                      className={styles.emptyStateBtn}
                      onClick={() => setIsLinkModalOpen(true)}
                    >
                      Vincular Primera Entidad
                    </button>
                  </div>
                ) : (
                  currentLinkedEntities.map((entity) => (
                    <div key={entity.id} className={styles.entityCard}>
                      <div className={styles.entityInfo}>
                        <h4 className={styles.entityName}>{entity.entity_name}</h4>
                        <div className={styles.entityMeta}>
                          <span className={`${styles.permissionBadge} ${styles[entity.permission.toLowerCase()]}`}>
                            {entity.permission === 'OWNER' ? 'Propietario' : 'Solo Lectura'}
                          </span>
                          {entity.is_primary && (
                            <span className={styles.primaryBadge}>Principal</span>
                          )}
                        </div>
                      </div>
                      <button
                        className={styles.removeBtn}
                        onClick={() => handleRemoveLink(entity.id)}
                        title="Eliminar Vinculación"
                      >
                        <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                          <line x1="18" y1="6" x2="6" y2="18" />
                          <line x1="6" y1="6" x2="18" y2="18" />
                        </svg>
                      </button>
                    </div>
                  ))
                )}
              </div>
            </>
          ) : (
            <div className={styles.emptySelection}>
              <p>Seleccione un usuario para ver sus entidades vinculadas</p>
            </div>
          )}
        </div>
      </div>

      {/* Link Entity Modal */}
      {isLinkModalOpen && selectedUser && (
        <LinkEntityModal
          userId={selectedUser}
          onClose={() => setIsLinkModalOpen(false)}
          onSubmit={handleLinkEntity}
        />
      )}
    </div>
  );
}

