'use client';

import { useState } from 'react';
import EditGroupModal from './EditGroupModal';
import styles from './EstructuraEquipos.module.css';

interface ClientGroup {
  id: string;
  group_name: string;
  chief: {
    id: string;
    first_name: string;
    last_name: string;
  };
  accountants: Accountant[];
}

interface Accountant {
  id: string;
  staff_id: string;
  first_name: string;
  last_name: string;
  avatar?: string;
  client_count: number;
}

interface OrphanClient {
  id: string;
  entity_name: string;
}

export default function EstructuraEquipos() {
  const [groups, setGroups] = useState<ClientGroup[]>([
    {
      id: '1',
      group_name: 'Grupo A',
      chief: {
        id: '1',
        first_name: 'Carlos',
        last_name: 'Ramírez',
      },
      accountants: [
        {
          id: '1',
          staff_id: '2',
          first_name: 'María',
          last_name: 'González',
          client_count: 12,
        },
        {
          id: '2',
          staff_id: '3',
          first_name: 'Juan',
          last_name: 'Pérez',
          client_count: 18,
        },
      ],
    },
    {
      id: '2',
      group_name: 'Grupo B',
      chief: {
        id: '4',
        first_name: 'Ana',
        last_name: 'Martínez',
      },
      accountants: [
        {
          id: '3',
          staff_id: '5',
          first_name: 'Laura',
          last_name: 'Sánchez',
          client_count: 25,
        },
        {
          id: '4',
          staff_id: '6',
          first_name: 'Pedro',
          last_name: 'López',
          client_count: 8,
        },
      ],
    },
  ]);

  const [orphanClients, setOrphanClients] = useState<OrphanClient[]>([
    { id: '1', entity_name: 'Cliente Sin Asignar 1' },
    { id: '2', entity_name: 'Cliente Sin Asignar 2' },
    { id: '3', entity_name: 'Cliente Sin Asignar 3' },
  ]);

  const [editingGroup, setEditingGroup] = useState<ClientGroup | null>(null);
  const [draggedClient, setDraggedClient] = useState<OrphanClient | null>(null);

  const getBorderColor = (clientCount: number): string => {
    if (clientCount < 15) return 'green';
    if (clientCount <= 25) return 'orange';
    return 'red';
  };

  const getInitials = (firstName: string, lastName: string): string => {
    return `${firstName.charAt(0)}${lastName.charAt(0)}`.toUpperCase();
  };

  const handleEditGroup = (group: ClientGroup) => {
    setEditingGroup(group);
  };

  const handleCloseEditModal = () => {
    setEditingGroup(null);
  };

  const handleUpdateChief = async (groupId: string, newChiefId: string) => {
    // TODO: Implement API call to update chief
    setGroups(groups.map(g => 
      g.id === groupId 
        ? { ...g, chief: { id: newChiefId, first_name: 'Nuevo', last_name: 'Jefe' } }
        : g
    ));
    setEditingGroup(null);
  };

  const handleDragStart = (client: OrphanClient) => {
    setDraggedClient(client);
  };

  const handleDragEnd = () => {
    setDraggedClient(null);
  };

  const handleDropOnAccountant = async (accountantId: string, staffId: string) => {
    if (!draggedClient) return;

    // TODO: Implement API call PATCH /entities/{id}
    // await fetch(`/api/entities/${draggedClient.id}`, {
    //   method: 'PATCH',
    //   body: JSON.stringify({ assigned_accountant_id: staffId }),
    // });

    // Remove from orphan list and update accountant count
    setOrphanClients(orphanClients.filter(c => c.id !== draggedClient.id));
    setGroups(groups.map(group => ({
      ...group,
      accountants: group.accountants.map(acc => 
        acc.id === accountantId 
          ? { ...acc, client_count: acc.client_count + 1 }
          : acc
      ),
    })));
    setDraggedClient(null);
  };

  return (
    <div className={styles.estructuraEquipos}>
      <div className={styles.boardContainer}>
        {/* Kanban Board */}
        <div className={styles.kanbanBoard}>
          {groups.map((group) => (
            <div key={group.id} className={styles.groupColumn}>
              <div className={styles.groupHeader}>
                <div className={styles.groupHeaderInfo}>
                  <h3 className={styles.groupName}>{group.group_name}</h3>
                  <p className={styles.chiefName}>
                    Jefe: {group.chief.first_name} {group.chief.last_name}
                  </p>
                </div>
                <button
                  className={styles.editGroupBtn}
                  onClick={() => handleEditGroup(group)}
                  title="Editar Grupo"
                >
                  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7" />
                    <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z" />
                  </svg>
                </button>
              </div>

              <div className={styles.accountantsList}>
                {group.accountants.map((accountant) => (
                  <div
                    key={accountant.id}
                    className={`${styles.accountantCard} ${styles[getBorderColor(accountant.client_count)]}`}
                    data-staff-id={accountant.staff_id}
                    onDragOver={(e) => {
                      e.preventDefault();
                      e.currentTarget.classList.add(styles.dragOver);
                    }}
                    onDragLeave={(e) => {
                      e.currentTarget.classList.remove(styles.dragOver);
                    }}
                    onDrop={(e) => {
                      e.preventDefault();
                      e.currentTarget.classList.remove(styles.dragOver);
                      handleDropOnAccountant(accountant.id, accountant.staff_id);
                    }}
                  >
                    <div className={styles.accountantAvatar}>
                      {accountant.avatar ? (
                        <img src={accountant.avatar} alt={`${accountant.first_name} ${accountant.last_name}`} />
                      ) : (
                        <span>{getInitials(accountant.first_name, accountant.last_name)}</span>
                      )}
                    </div>
                    <div className={styles.accountantInfo}>
                      <p className={styles.accountantName}>
                        {accountant.first_name} {accountant.last_name}
                      </p>
                      <span className={styles.clientBadge}>
                        {accountant.client_count} Clientes
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>

        {/* Orphan Clients Sidebar */}
        <div className={styles.orphanSidebar}>
          <h3 className={styles.orphanTitle}>Clientes Sin Asignar</h3>
          <div className={styles.orphanList}>
            {orphanClients.map((client) => (
              <div
                key={client.id}
                className={styles.orphanClient}
                draggable
                onDragStart={() => handleDragStart(client)}
                onDragEnd={handleDragEnd}
              >
                <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                  <path d="M21 10c0 7-9 13-9 13s-9-6-9-13a9 9 0 0 1 18 0z" />
                  <circle cx="12" cy="10" r="3" />
                </svg>
                <span>{client.entity_name}</span>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Edit Group Modal */}
      {editingGroup && (
        <EditGroupModal
          group={editingGroup}
          onClose={handleCloseEditModal}
          onUpdate={handleUpdateChief}
        />
      )}
    </div>
  );
}

